.gpt3__header {
  display: flex;
  flex-direction: row;
}

.gpt3__header-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;

  margin-right: 5rem;
}

.gpt3__header-content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  /* font-size: 40px;
  line-height: 40px; */
  letter-spacing: -0.04em;
}

.gpt3__header-content p {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  color: var(--color-text);

  margin-top: 1.5rem;
}

.gpt3__header-content__input {
  width: 40%;
  margin: 2rem 0 1rem;

  display: flex;
  flex-direction: row;
}
.header-row {
  width: 40%;
  margin: 2rem 0 1rem;
  z-index: 2;
  display: flex;
  flex-direction: row;
}

.gpt3__header-content__input input {
  flex: 2;
  width: 100%;
  min-height: 50px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  background: var(--color-footer);
  border: 2px solid var(--color-footer);
  padding: 0 1rem;
  outline: none;
  color: #fff;

  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.gpt3__header-content__input button {
  flex: 0.6;
  width: 30%;
  min-height: 50px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  background: #ff4820;
  border: 2px solid #ff4820;
  padding: 0 1rem;
  color: #fff;
  cursor: pointer;
  outline: none;

  border-radius: 5px;
}

.gpt3__header-content__people {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;

  margin-top: 2rem;
}

.gpt3__header-content__people img {
  width: 181.79px;
  height: 38px;
}

.gpt3__header-content__people p {
  margin: 0 0 0 1rem;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 12px;
  line-height: 38px;
  color: #fff;
  text-align: center;
}

.gpt3__header-image {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.gpt3__header-image img {
  width: 80%;
  /* height: 80%; */
}
.gpt3__header-image2 img {
  width: 0px;
  height: 0px;
}

@media screen and (max-width: 1050px) {
  .gpt3__header {
    flex-direction: column;
  }

  .gpt3__header-content {
    margin: 0 0 3rem;
  }
}

@media screen and (max-width: 650px) {
  .gpt3__header-content h1 {
    /* font-size: 48px; */
    /* line-height: 60px; */
  }

  .gpt3__header-content p {
    font-size: 16px;
    line-height: 24px;
  }

  .gpt3__header-content__people {
    flex-direction: column;
  }

  .gpt3__header-content__people p {
    margin: 0;
  }

  .gpt3__header-content__input input,
  .gpt3__header-content__input button {
    font-size: 16px;
    line-height: 24px;
  }
}

@media screen and (max-width: 490px) {
  .gradient__text-header {
    height: 200px;
  }
  .gradient__text-header2 span {
    font-size: 30px !important;
    line-height: 30px !important;
  }
  .gpt3__header-content h1 {
    height: 200px !important;
    font-size: 30px !important;
    line-height: 30px !important;
  }
  .gpt3__header-content h1 {
    font-size: 36px;
    line-height: 48px;
  }

  .gpt3__header-content p {
    font-size: 14px;
    line-height: 24px;
  }

  .gpt3__header-content__input input,
  .gpt3__header-content__input button {
    font-size: 12px;
    line-height: 16px;
  }
  .gpt3__header-image img {
    width: 0px;
    height: 0px;
  }
  .gpt3__header-image2 img {
    width: 80%;
    height: 80%;
    margin-bottom: 5rem;
  }
  .gpt3__header-image2 {
    margin-top: 2rem;
  }
  .footer-logo {
    height: 70px !important;
  }
  .footer-logo2 {
    height: 30px !important;
  }
  .footer-logo2:hover {
    opacity: 0.6 !important;
  }
  .footer-logo3 {
    width: 70px !important;
    object-fit: contain;
  }
  .footer-logo4 {
    width: 120px !important;
  }
  .gpt3__footer-links div {
    width: auto;
  }
  .header-logo {
    width: 0px;
  }
  .text-slate-200 {
    font-size: 12px;
  }
  .roadmap {
    margin: 5px !important;
    width: auto !important;
    padding: 3px !important;
  }
  .gpt3__navbar-links_logo {
    margin-right: 0px !important;
  }
  .gpt3__footer-links {
    flex-direction: column;
  }
  .header-row {
    width: 100%;
  }
  .btn {
    width: 100% !important;
  }
}
@media screen and (max-width: 350px) {
  .gpt3__header-content h1 {
    height: 230px !important;
    font-size: 25px !important;
    line-height: 25px !important;
  }
  .gradient__text-header2 span {
    font-size: 25px !important;
    line-height: 25px !important;
  }
  .horde-icon2 {
    margin-bottom: 1rem;
  }
}
